/* Spinner.css */
.loader {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  
  .bar {
    display: inline-block;
    width: 3px;
    height: 20px;
    background-color: #5491bf;
    border-radius: 10px;
    animation: scale-up4 1s linear infinite;
  }
  
  .bar:nth-child(2) {
    height: 35px;
    margin: 0 5px;
    animation-delay: 0.25s;
  }
  
  .bar:nth-child(3) {
    animation-delay: 0.5s;
  }
  
  @keyframes scale-up4 {
    20% {
      background-color: #91b0c7;
      transform: scaleY(1.5);
    }
    40% {
      transform: scaleY(1);
    }
  }
  